import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ScreenSpinner, Spinner, Group, Div, Avatar, PanelHeader } from '@vkontakte/vkui';
import '../style.css';


class Feed extends React.Component {
    
    constructor(props) {
      super(props);
      this.state = {
        jokes: [],
        isFetching : true
      };
    }
    componentDidMount() {
        this.loadJokes();
        
    }
    loadJokes(){
        //fetch('http://92.240.135.238:5000/')
        fetch('https://api.icndb.com/jokes/random/10')
        .then(response => response.json())
        .then(data => {this.setState({ jokes: data.value});
       // .then(data => {this.setState({ jokes: data.value});
        });
        console.log(this.state.jokes);
        this.state.isFetching = false;
    }
    /*loadJokes(){
        fetch('http://92.240.135.238:5000/')
        .then(response => response.json())
        .then(data => {this.setState({ jokes: data});
        });
        this.state.isFetching = false;
    }*/
  
    render() {
        
        const {jokes} = this.state;
        const {isFetching} = this.state; 
        return(
            <Panel id="feed">
                <PanelHeader >Лента</PanelHeader>
                {isFetching &&  <ScreenSpinner className= "spinner" size="large" style={{ marginTop: 20 }}/>}
                {jokes.map(joke =>
                    <Group className="post">
                        {joke.joke}
                    </Group>
                )}
          </Panel>
            
        )
           
    }
};


export default Feed;
